
















































































	import mpWeixin from '@/static/data/mp-weixin.js';
    import mInput from '../../components/m-input.vue';
	import {mapState, mapMutations} from 'vuex'
	
    export default {
        components: {mInput},
        data() {
			return {
				bg:this.$http.imgPath + "manage/register-bg.png",
			    providerList: [],
			    hasProvider: false,
				provider:'',
				openId:'',
			    nickname: '',
				avatarUrl:'',
				mobile: '',
				code: '',
			    password: '',
			    passwordRepeat: '',
				shortCodeBtn:{
					disabled:false,
					countdown:0			//倒计时
				},
				shortCode:{},
				submitBtn:{disabled:false}
			}
        },
		computed: {
			...mapState(['shareFrom'])
		},
        methods: {
			...mapMutations(['register']),
			getShortCode:function(){
				if (this.mobile.length != 11) {
				    uni.showToast({
				        icon: 'none',
				        title: '手机号有误'
				    });
				    return;
				}
				//
				let interval = 0;
				setTimeout(() => { 
					this.shortCodeBtn.disabled = false;
					if(interval > 0){
						clearInterval(interval);
					}
				}, 60000)
				
				if(this.shortCodeBtn.disabled){
					return;
				}else{
					this.shortCodeBtn.disabled = true;
					this.shortCodeBtn.countdown = 60;
					interval = setInterval(() =>{
						this.shortCodeBtn.countdown--;
					}, 1000)
					
				}
				
				let that = this;
				uni.request({
					url: this.$http.contextPath + 'account/sendShortCode',
					method: 'POST',
					data:{
						mobile:this.mobile
					},
					success: (res) => {
						if(that.$http.success(res)){
							that.shortCode = res.data.data;
							uni.showToast({
								duration:3000,
								title:'验证码：'+that.shortCode.code
							})
						}
					}
				})
			},
			verify(){









				if (this.password.length < 6) {
				    uni.showToast({
				        icon: 'none',
				        title: '密码最短为 6 个字符'
				    });
				    return;
				}
				// if (this.password != this.passwordRepeat) {
				//     uni.showToast({
				//         icon: 'none',
				//         title: '密码不一致'
				//     });
				//     return;
				// }
				if (this.mobile.length != 11) {
				    uni.showToast({
				        icon: 'none',
				        title: '手机号有误'
				    });
				    return;
				}
				//校验手机验证码
				if(!this.verifyCode(this.mobile, this.code)){
					return;
				}
				
				return true;
			},
			verifyCode(mobile, code){
				let title = '';
				if(code == ''){
					title = '请输入验证码';
				}else if(this.shortCode.mobile == mobile && this.shortCode.code == code){
					return true;
				}else{
					title = '验证码有误';
				}
				uni.showToast({
					icon: 'none',
					title:title
				})
				return false;
			},
            bindRegister() {
				if(!this.verify())return;
				
				//
				setTimeout(() => { 
					this.submitBtn.disabled = false;
				}, 1000)
				
				if(this.submitBtn.disabled){
					return;
				}else{
					this.submitBtn.disabled = true;
				}
				
				this.submit();
// 				uni.redirectTo({
// 					url:'mobileVerify?provider=mobile&nickname='+data.nickname+'&mobile='+data.mobile+'&password='+data.password
// 				})
            },
			initProvider() {
			    const filters = ['weixin', 'qq', 'weibo'];
			    uni.getProvider({
			        service: 'oauth',
			        success: (res) => {
			            if (res.provider && res.provider.length) {
			                for (let i = 0; i < res.provider.length; i++) {
			                    if (~filters.indexOf(res.provider[i])) {
			                        this.providerList.push({
			                            value: res.provider[i],
			                            image: '../../static/img/logos/' + res.provider[i] + '.png'
			                        });
			                    }
			                }
			                this.hasProvider = true;
			            }
			        },
			        fail: (err) => {
			            console.error('获取服务供应商失败：' + JSON.stringify(err));
			        }
			    });
			},
			oauth(value) {				
			    uni.login({
			        provider: value,
			        success: (res) => {
			            uni.getUserInfo({
			                provider: value,
			                success: (infoRes) => {
			                    /**
			                     * 实际开发中，获取用户信息后，需要将信息上报至服务端。
			                     * 服务端可以用 userInfo.openId 作为用户的唯一标识新增或绑定用户信息。
			                     */
								console.log(infoRes);
								uni.navigateTo({
									url:'mobileVerify?provider='+value+'&nickname='+infoRes.userInfo.nickName+'&openId='+infoRes.userInfo.openId+"&avatarUrl="+infoRes.userInfo.avatarUrl
								})
			                },
							fail(res) {
								console.log(res);
							}
			            });
			        },
			        fail: (err) => {
			            console.error('授权登录失败：' + JSON.stringify(err));
			        }
			    });
			},
			getuserinfo(res){
				if(!this.verify())return;
				
				let that = this;
				uni.login({
					provider: 'weixin',
					success(e) {
						//获取openID
						uni.request({
							url: mpWeixin.openIdUrl + e.code,
							method: 'GET',
							success: (sess) =>{
								let userInfo = res.detail.userInfo;
								that.provider = 'Weixin';
								that.nickname = userInfo.nickName;
								that.avatarUrl = userInfo.avatarUrl;
								that.openId = sess.data.openid;
								console.log("openId:"+that.openId);
								that.submit();
// 								uni.redirectTo({
// 									url:'mobileVerify?provider=weixin&nickname='+userInfo.nickName+'&openId='+sess.data.openid+"&avatarUrl="+userInfo.avatarUrl
// 								})
							},
							fail(e) {
								uni.showToast({
									title:'获取信息错误'
								})
							}
						})
					},
					fail(e) {
						uni.showToast({
							title:'登陆错误'
						})
					}
				})
			},
			submit:function(){
				//
				let param = {
					nickname:this.nickname,
					password:this.password,
					avatarUrl:this.avatarUrl,
					mobile: this.mobile,
					// fromMemberId:this.shareFrom.memberId,
					fromAccountId:this.shareFrom.accountId
				};
				if(this.openId != ''){
					param['openId'+this.provider] = this.openId;
				}
				let that = this;
				uni.request({
					url: this.$http.contextPath + 'account/register',
					method: 'POST',
					data:param,
					success: (res) => {
						if(that.$http.success(res)){
							that.regSuccess(res.data.data);
						}
					},
					fail: (e) => {
						uni.showToast({
							title:"服务器响应错误"
						})
					}
				})
			},
			regSuccess(account){
				this.register(account);
				uni.showToast({
				    title: '注册成功'
				});
				//跳转登录
				uni.redirectTo({
					url: '../login/login'
				});
			}
        },
		onLoad() {
			// this.initProvider();
		}
    }
